<template>
	<view class="my-settings_container">
		<cu-custom bgColor="bg-gradual-green" :isBack="true">
			<view slot="backText">返回</view>
			<view slot="content">设置</view>
		</cu-custom>
		<view class="cu-list menu">
			<view class="cu-item arrow">
				<view class="content">
					<text class="text-grey">头像</text>
				</view>
				<view class="action">
					<view class="cu-avatar-group">
						<image class="cu-avatar_self" src="https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg" mode="aspectFill"></image>
					</view>
				</view>
			</view>
			<view class="cu-item arrow" @click="goPage('/pages/my/settings/nickname')">
				<view class="content">
					<text class="text-grey">昵称</text>
				</view>
				<view class="action">
					<text class="text-grey text-sm">张三</text>
				</view>
			</view>
			<view class="cu-item arrow">
				<view class="content">
					<text class="text-grey">性别</text>
				</view>
				<picker @change="onPickerChange" :value="index" :range="picker">
					<view class="picker">
						{{ picker[index] }}
					</view>
				</picker>
			</view>
			<view class="cu-item arrow" @click="goPage('/pages/my/settings/sign')">
				<view class="content">
					<text class="text-grey">个性签名</text>
				</view>
				<view class="action">
					<text class="text-grey text-sm">小目标还没有实现！</text>
				</view>
			</view>
			<view class="cu-item arrow" @click="goPage('/pages/my/settings/addressList')">
				<view class="content">
					<text class="text-grey">我的地址</text>
				</view>
			</view>
			<view class="cu-item arrow" @click="goPage('/pages/my/settings/phone')">
				<view class="content">
					<text class="text-grey">手机号码</text>
				</view>
				<view class="action">
					<text class="text-grey text-sm">15325125411</text>
				</view>
			</view>
			<view class="cu-item arrow" @click="goPage('/pages/my/settings/wechart')">
				<view class="content">
					<text class="text-grey">微信二维码</text>
				</view>
				<view class="action">
					<text class="cuIcon-qr_code lg text-gray"></text>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				index: 0,
				picker: ['男', '女'],
			}
		},
		created() {
		
		},
		methods: {
			onPickerChange(e) {
				this.index = e.detail.value
			},
			goPage(url) {
				uni.navigateTo({
					url
				})
			}
		}
	}
</script>

<style lang="scss" lang="scss">
	.cu-avatar_self {
		width: 80upx;
		height: 80upx;
		border-radius: 10upx;
	}
</style>
